<template>
<div class="First-draft">
  <Row type="flex" justify="space-around" class="code-row-bg search_criteria">
      <Col :xs="12" :sm="12" :lg="6">
        <p>{{$t("common.Rating_project_number")}}&nbsp</p>
        <Input placeholder="" style="width: 120px"></Input>
      </Col>
      <Col :xs="12" :sm="12" :lg="6">
        <p>{{$t("common.Rating_enterprise_name")}}&nbsp</p>
        <Input placeholder="" style="width: 120px"></Input>
      </Col>
      <Col :xs="12" :sm="12" :lg="5">
        <p>{{$t("common.Approval_Status")}}&nbsp</p>
        <Select style="width:120px; margin-right: 10px;">
            <Option v-for="item in stateList" :value="item.value" :label="item.label" :key="item.value"></Option>
        </Select>
      </Col>
      <Col :xs="12" :sm="12" :lg="7">
        <p>{{$t("common.Corporate_credit_rating")}}&nbsp</p>
        <Select style="width:120px; margin-right: 10px;">
            <Option v-for="item in levelList" :value="item.value" :label="item.label" :key="item.value"></Option>
        </Select>
        <Button type="primary">{{$t("common.Query")}}</Button>
      </Col>
      
  </Row>
  <Table highlight-row :columns="columns1" :data="historyData" @on-row-click="onedata" :style="{marginTop:'20px'}"></Table>
  <Row type="flex" justify="center" class="code-row-bg page-data">
    <Col span="24">
      <Page :total="dataCount" :page-size="pageSize" @on-change="changepage"></Page>
    </Col>
  </Row>
</div>
</template>

<script>
export default {
    name:'First_draft',
    data () {
        return {
            ajaxHistoryData:[],
              // 初始化信息总条数
            dataCount:0,
              // 每页显示多少条
            pageSize:10,
            historyData: [],
            stateList: [
                {
                    value: 'All',
                    label: this.$t("common.All")
                },
                {
                    value: 'Release',
                    label: this.$t("common.Rating_report_released")
                },
                {
                    value: 'No',
                    label: this.$t("common.Review")
                },
                {
                    value: 'Yes',
                    label: this.$t("common.End")
                }
            ],
            levelList: [
                {
                    value: 'All',
                    label: this.$t("common.All")
                },
                {
                    value: 'Yes',
                    label: 'AAA'
                },
                {
                    value: 'No',
                    label: 'AA'
                }
            ],
            columns1: [
                {
                    title: this.$t("common.Rating_project_number"),
                    key: 'project_number',
                    align:'center'
                },
                {
                    title: this.$t("common.Rating_project_name"),
                    key: 'project_name',
                    align:'center',
                    render: (h, params) => {
                      return h('a', {
                          style: {
                              color:'#2d8cf0'
                          },
                          on: {
                            click: () => {
                              this.$router.push('/Rating_details')
                            } 
                          }
                      }, params.row.project_name)
                    }
                },
                {
                    title: this.$t("common.Rating_enterprise_name"),
                    key: 'company_name',
                    align:'center',
                },
                {
                    title: this.$t("common.Corporate_credit_rating"),
                    key: 'level',
                    align:'center',
                },
                {
                    title: this.$t("common.Informal_rating_report"),
                    key: 'Informal_report',
                    align:'center',
                },
                {
                    title: this.$t("common.User_objection_view"),
                    key: 'see',
                    align:'center',
                },
                {
                    title: this.$t("common.Rating_status"),
                    key: 'state',
                    align:'center',
                }
            ],
            data1: this.mockTableData1 (),
            Personal_info:null
        }
    },
    methods:{
      handleListApproveHistory(){
          // 保存取到的所有数据
          this.ajaxHistoryData = this.data1
          this.dataCount = this.data1.length;
          // 初始化显示，小于每页显示条数，全显，大于每页显示条数，取前每页条数显示
          if(this.data1.length < this.pageSize){
              this.historyData = this.ajaxHistoryData;
          }else{
              this.historyData = this.ajaxHistoryData.slice(0,this.pageSize);
          }
      },
      changepage(index){
          var _start = ( index - 1 ) * this.pageSize;
          var _end = index * this.pageSize;
          this.historyData = this.ajaxHistoryData.slice(_start,_end);
      },
      mockTableData1 () {
          let data = [];
          for (let i = 1; i < 101; i++) {
              data.push({
                  project_number: 'DG2018020300'+i,
                  project_name: parseInt(Math.random()*(3-1)+0)>0?'福建六建集团有限公司'+'DG2018020300'+i:'鸿兴资本运营有限公司'+'DG2018020300'+i,
                  company_name: parseInt(Math.random()*(3-1)+0)>0?'福建六建集团有限公司':'鸿兴资本运营有限公司',
                  state:parseInt(Math.random()*(3-1)+0)>0?'复评':parseInt(Math.random()*(3-1)+0)>0?'评级报告发布':'正常结项',
                  level: parseInt(Math.random()*(3-1)+0)>0?'AAA':'AA',
                  Informal_report:'-',
                  see:'-'

              })
          }
          return data;
      },
      getRandomDateBetween() { // 要求a, b 都是Date类型, 且a < b
          let date = new Date(),
          a = + new Date("October 10, 2017 11:13:00"),
          b = + new Date();
          date.setTime(Math.random()*(b - a) + a);
          return date;
      },
      formatDate (date) {
          const y = date.getFullYear();
          let m = date.getMonth() + 1;
          m = m < 10 ? '0' + m : m;
          let d = date.getDate();
          d = d < 10 ? ('0' + d) : d;
          let h = date.getHours();
          h = h < 10 ? ('0' + h) : h;
          let M = date.getMinutes();
          M = M < 10 ? ('0' + M) : M;
          let s = date.getSeconds();
          s = s < 10 ? ('0' + s) : s;
          return y + '-' + m + '-' + d + ' ' + h + ':' + M + ':' + s;
      },
      formatTime (date) {
          const y = date.getFullYear();
          let m = date.getMonth() + 1;
          m = m < 10 ? '0' + m : m;
          let d = date.getDate();
          d = d < 10 ? ('0' + d) : d;
          return y + '-' + m + '-' + d;
      },
      onedata (data,index){
        //console.log(data,index)
        //this.Personal_info = data
        //this.sendParams ()
        //this.$router.push('/Rating_details')
      },
      sendParams () {
          this.$router.push({
              path: '/Institutional_info',
              name: 'Institutional_info',
              params: {
                  name: 'Personal-info',
                  send_data: this.Personal_info
              }
              /*query: {
                name: 'Personal-info',
                send_data: this.Personal_info
              }*/
          })
       }
    },
    created(){
      this.handleListApproveHistory();
    }
}
</script>

<style lang="css">
.First-draft{
  
}
.First-draft .search_criteria{
  background-color: #fff;
  line-height: 50px;
  text-align: center;
}
.First-draft .search_criteria p{
  height: 30px;
}
.First-draft .page-data{
  height: 50px;
  line-height: 50px;
  border: 1px solid #DDDEE1;
  border-top: none;
  text-align: center;
  background-color: #fff;
}
</style>



